<template>
    <section class='material_view wrap'>
        <div class='head'>
            <h2><span class='sub_tit'>{{content.company}}</span> - {{content.name}} </h2>
            <p>{{content.exp}}</p>
            <img v-bind:src="content.img">
        </div>
        <div class='body'>
            <div v-html="content.desc" class='desc'></div>
            <div class='file' v-if="content.file"><a v-bind:href="content.file" target='blank'> </a>
                <b-icon icon="download"></b-icon>
            </div>
        </div>
        <div class='btns'>
            <router-link tag='div' to='/information/info4' class='btn'></router-link>
        </div>
    </section>
</template>

<script>
    export default {
        props: ['idx'],
        metaInfo() {
            return {
                title: '',
                titleTemplate: `%s | ${this.content.company} - ${this.content.name}`, // title     ( ) 
                meta: [{
                        name: 'description',
                        content: `30  ()         . 30 1000    ,      () .`
                    },
                    {
                        name: 'keyword',
                        content: `${this.content.name}, PLS, pls ,,,,,,,,,,`
                    },
                ],
            }
        },
        data() {

            return {
                content: Array
            }
        },
        created() {
            this.$Axios.post('http://13.124.215.190/admin/api/farm_item/zoom', {
                    idx: this.idx
                })
                .then((result) => {
                    this.content = result.data.result[0]
                    // 

                    this.$Axios.post(
                        'http://13.124.215.190/admin/api/farm_item/join', {
                            idx: this.content.idx,
                            join: this.content.join + 1
                        })
                    // 
                })
        },
    }
</script>

<style lang="scss">
    section.material_view {
        div.head {
            margin-bottom: 20px;
            text-align: center;

            img {
                max-height: 350px;
                margin: 20px 0px;
            }
        }

        div.body {
            border-bottom: 1px solid #d0d0d0;
            padding-bottom: 50px;

            div.desc {
                img {
                    max-width: 100%;
                    display: block;
                    margin: 0 auto;
                }

                * {
                    text-align: inherit;
                }
            }

            div.file {
                text-align: center;
                font-weight: bold;

                a {
                    margin-right: 5px;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }

        div.btns {
            text-align: right;
        }
    }
</style>