<template>
    <section class='material wrap'>
        <h2> </h2>
            <ul class='nav'>
                <li
                v-bind:class="{active:mode==''}"
                @click="filterData('')"
                ></li>

                <li v-for="(item,i) in Cate"
                    :key='i'
                    @click="filterData(item)"
                    v-bind:class="{active:mode==item}"
                    >{{item}}</li>

                    </ul>

                          <ul class='content_main'>
                        <li 
                            v-for = "(content,i) in contents" 
                            :key='i'
                            @click="listView(content.idx,content.join)"
                            >
                            
                            <div class='img'>
                                <img v-bind:src="content.img">
                            </div>
                            <div class='text'>
                                <h4>{{content.name}}({{content.company}})</h4>
                                <p>{{content.exp}}</p>
                                <p class='bottom'>

                                <!-- <span>
                                    : 
                                    {{content.join}}
                                </span> ()-->
                                
                                <span class='date'>
                                    {{$moment(content.date).format('YYYY-MM-DD')}}
                                </span>
                                
                                </p>
                            </div>
                        </li>
                        <h3 v-if="contents == ''">  .</h3>
                    </ul>
    </section>
</template>

<script>
export default {
    metaInfo(){
      return{
        title:'',
        titleTemplate: `%s |  `, // title     ( ) 
        meta:[
          {name: 'description', content:`30  ()         . 30 1000    ,      () .`},
          {name: 'keyword', content:`PLS, pls ,,,,,,,,,,`},
        ],
      }
    },
    data(){
        return{
            mode:'',
            lists:Array,
            contents:Array,
            Cate:["","","","","","","","","",""],
            limit:9,
        }
    },
    watch: {
        mode(){
            if(this.mode == ''){
                this.contents = this.lists
            }
            else{
                this.contents = this.lists.filter((x)=>{
                    return x.company == this.mode
                })
            }
        }
    },
    
    created() {
        this.$Axios.get('http://13.124.215.190/admin/api/farm_item')
        .then((result)=>{

            let Lists = result.data.result.filter((x)=>{
                return x.active == 1
            })
            
            this.lists = Lists;
            this.contents = Lists.slice(0,9);

        })
    },
    mounted(){
        document.addEventListener('scroll', () => {
            this.getDistBottom()
        })
    },
    methods:{
        filterData(cate){
            this.mode = cate
        },
        listView(idx,join){
            this.$router.push({
                path:`info4/zoom/${idx}` 
            })
        },
        getDistBottom() {
            let scrollPosition = window.pageYOffset;
            let windowSize = window.innerHeight;
            let bodyHeight = document.body.offsetHeight;

            let bottom = Math.max(bodyHeight - (scrollPosition + windowSize), 0)

            if (bottom <= 1600) {
                this.limit+=3;
                this.contents = this.contents.slice(0,this.limit)
            }
        }
    }

}
</script>

<style lang="scss">
@import './common.scss';

</style>