<template>
    <div>
        <nav class='lnb faq_lnb'>
            <div class='wrap'>
                <ul>
                    <li v-for="(listCate,i) in listCates" :key='i' @click="filterData(listCate)"
                        v-bind:class="{active:listCate.value}">
                        <span v-if="listCate.Name=='all'"></span>
                        <span v-if="listCate.Name=='pro'">Win-Win pro</span>
                        <span v-if="listCate.Name=='pos'">Win-Win pos</span>
                        <span v-if="listCate.Name=='etc'"></span>
                    </li>
                </ul>
            </div>
        </nav>

        <section class='wrap'>
            <h2>   FAQ</h2>
            <table>
                <thead>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                </thead>
                <tbody>
                    <router-link tag='tr' v-bind:to="'/board/zoomfaq/'+filter.idx" v-for="(filter,i) in filters"
                        v-if='i < limit && i >= limit-Standard' :key='i'>
                        <td>{{i+1}}</td>
                        <td>
                            <span v-if="filter.cate=='pro'">win-win Pro</span>
                            <span v-else-if="filter.cate=='pos'">win-win </span>
                            <span v-else-if="filter.cate=='etc'"></span>
                        </td>
                        <td>{{filter.tit}}</td>

                        <td>{{$moment(filter.date).format('YYYY-MM-DD')}}</td>
                        <td>{{filter.join}}</td>

                    </router-link>
                </tbody>
            </table>

            <list-number :dataLength='filters.length' :standard="Standard" @child="parent" />

        </section>
    </div>
</template>

<script>
    export default {
        metaInfo() {
            return {
                title: '',
                titleTemplate: `%s |    `, // title     ( ) 
                meta: [{
                        name: 'description',
                        content: `30  ()         . 30 1000    ,      () .`
                    },
                    {
                        name: 'keyword',
                        content: `PLS, pls , ,, , ,  ,  `
                    },
                ],
            }
        },
        data() {
            return {
                Standard: 15,
                filterCate: 'all',
                listCates: [{
                        Name: 'all',
                        value: true
                    },
                    {
                        Name: 'pro',
                        value: false
                    },
                    {
                        Name: 'pos',
                        value: false
                    },
                    {
                        Name: 'etc',
                        value: false
                    },
                ],
                faq: '',
                filters: '',
                limit: 10,
                start: 0
            }
        },
        created() {
            const BaseData = "http://13.124.215.190/admin/api/faq"
            this.$Axios.get(BaseData)
                .then((result) => {
                    this.faqs = result.data.result;
                    this.filters = this.faqs;

                })
        },
        methods: {
            parent(data) {
                this.limit = data.page * this.Standard;
            },
            filterData(cates) {
                // this.ActiveCate = ''

                for (let i = 0; i < this.listCates.length; i++) {
                    this.listCates[i].value = false
                }

                cates.value = true
                if (cates.Name == 'all') {
                    this.filters = this.faqs
                } else {

                    this.filters = this.faqs.filter((x) => {
                        return x.cate == cates.Name
                    })
                }


                // eventBus.$emit('UpdateList', {
                //     DataLength: Math.ceil((this.filters.length) / 10),
                //     nowpage: this.limit - 10
                // })
                // this.start = 0
                // this.limit = 10
            },
        }
    }
</script>

<style lang="scss">
    @import './table.scss';

    nav.faq_lnb {
        border-bottom: 1px solid #d0d0d0;

        ul {
            width: 100%;
            height: 50px;
            text-align: center;

            li {
                cursor: pointer;
                width: 25%;
                float: left;
                height: 100%;
                border-left: 1px solid #d0d0d0;
                transition: .1s ease-in-out;
                box-sizing: border-box;
                line-height: 50px;

                &.active {
                    background-color: #d0d0d0;
                }

                &:last-child {
                    border-right: 1px solid #d0d0d0;
                }
            }
        }
    }
</style>