<template>
    <div>
        <section class='section1'>
            <div class='wrap'>
                <h2> </h2>
                <nav>
                    <div class='btn' @click="changeMode" v-if="writeMode"> </div>
                    <router-link tag='div' class='btn' v-if="boardMode" to='qnawrite/'></router-link>
                </nav>

                <table v-if="boardMode">
                    <thead>
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                    </thead>
                    <tbody>
                        <router-link tag='tr' v-bind:to="'zoomqna/'+board.idx" v-for="(board,i) in boards" :key='i'
                            v-if='i < limit && i >= limit-Standard'>
                            <td>{{i+1}}</td>


                            <td>{{board.status}}</td>

                            <td v-if="board.private" class='r_text'> </td>
                            <td v-else>{{board.title}}</td>
                            <td>{{board.writer}}</td>
                            <td>{{$moment(board.date).format('YYYY-MM-DD')}}</td>

                        </router-link>
                    </tbody>
                </table>
                <list-number :dataLength='boards.length' :standard="Standard" @child="parent" />
            </div>
        </section>
    </div>
</template>

<script>
    export default {
        metaInfo() {
            return {
                title: '',
                titleTemplate: `%s |  `, // title     ( ) 
                meta: [{
                        name: 'description',
                        content: `30  ()         . 30 1000    ,      () .`
                    },
                    {
                        name: 'keyword',
                        content: `PLS, pls , ,, , ,  ,  `
                    },
                ],
            }
        },
        data() {
            return {
                login: false,
                Standard: 15,
                //vuex   
                boardMode: true,
                writeMode: false,
                file: '',
                //true   false 
                limit: 10,
                start: 0,
                boards: ''
            }
        },
        created() {
            const BaseData = "http://13.124.215.190/admin/api/qna"
            this.$Axios.get(BaseData)
                .then((result) => {
                    this.boards = result.data.result;
                })
        },
        methods: {
            parent(data) {
                this.limit = data.page * this.Standard;
            },
            changeMode() {
                this.boardMode == true ? this.boardMode = false : this.boardMode = true
                this.writeMode == true ? this.writeMode = false : this.writeMode = true
            },
        }
    }
</script>

<style lang="scss">
    @import './table.scss';
</style>